@font-face {
  font-family: "icomoon";
  src: url("../../fonts/icomoon.eot?xan64c");
  src: url("../../fonts/icomoon.eot?xan64c#iefix") format("embedded-opentype"),
    url("../../fonts/icomoon.ttf?xan64c") format("truetype"),
    url("../../fonts/icomoon.woff?xan64c") format("woff"),
    url("../../fonts/icomoon.svg?xan64c#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-phone:before {
  content: "\e942";
}
.icon-facebook:before {
  content: "\ea91";
}
.icon-instagram:before {
  content: "\ea92";
}
.icon-whatsapp:before {
  content: "\ea93";
}
.icon-twitter:before {
  content: "\ea96";
}
.icon-youtube:before {
  content: "\ea9d";
}
